import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  Input,
  OnInit,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { InputBaseComponent } from '@backbase/ui-ang/base-classes';
import { DeprecationsService } from '@backbase/foundation-ang/future';
/* eslint-disable */
enum position {
  left = 'left',
  right = 'right',
}

export type LabelPosition = 'left' | 'right';

/**
 * @name SwitchComponent
 *
 * @description
 * Component that provides you a switch input.
 */
@Component({
  selector: 'bb-switch-ui',
  templateUrl: './switch.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      multi: true,
      useExisting: forwardRef(() => SwitchComponent),
    },
  ],
})
export class SwitchComponent extends InputBaseComponent implements OnInit {
  /**
   * The label position whether it is left or right.
   */
  @Input() labelPosition: LabelPosition = position.left;

  keyboardFocus = false;
  focused = false;

  constructor(
    protected readonly cd: ChangeDetectorRef,
    private el: ElementRef,
    protected readonly deprecationsService: DeprecationsService,
  ) {
    super(cd, deprecationsService);
  }

  ngOnInit(): void {
    super.ngOnInit();
    this.role = this.role || 'switch';
  }

  toggleSwitch($event: any): void {
    if ($event.target?.tagName === 'LABEL') {
      return;
    }

    const isIE11 = !!(window as any).MSInputMethodContext && !!(document as any).documentMode;
    if (!isIE11) {
      this.onValueChange(!this.value);
      this.el.nativeElement.dispatchEvent(new Event('change', { bubbles: true }));
    } else {
      if ($event.target.type === 'checkbox') {
        this.onValueChange();
      } else {
        this.onValueChange(!this.value);
      }
      const event = document.createEvent('Event');
      event.initEvent('change', true, false);
      this.el.nativeElement.dispatchEvent(event);
    }
  }

  onBlur($event: FocusEvent) {
    this.focused = false;
    super.onBlur($event);
  }

  isLabelLeft(): boolean {
    return this.labelPosition === position.left;
  }

  isLabelRight(): boolean {
    return this.labelPosition === position.right;
  }
}
